.App {
  position: relative;
  width: 100%;
  height: 100vh;
  background-image: url(../public/skm.jpg);
  background-size: 100% 100%;
}

.warning {
  position: absolute;
  top: 148px;
  left: 50%;
  transform: translateX(-50%);
  width: 95%;
  height: 30px;
  padding: 0;
  overflow: hidden;
  background-color: #fefaee;
}

.marque {
  position: absolute;
  top: 0;
  width: 95%;
  height: 30px;
  margin: 0;
  font-size: 20px;
  line-height: 30px;
  color: #eca66a;
  font-weight: bold;
  white-space: nowrap;
  animation: marque 12s linear infinite;
}

@keyframes marque {
  0% {
    left: 100%;
  }

  100% {
    transform: translateX(-100%);
    left: 0;
  }
}

.time {
  position: absolute;
  top: 200px;
  left: 50%;
  transform: translateX(-50%);
  width: 92%;
  height: 70px;
  padding: 0;
  background-color: #fff;
  color: #343a4e;
  text-align: center;
  font-size: 42px;
  font-weight: bold;
  line-height: 70px;
}